import { useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE } from 'recoil';
import { profileQuery, transactionsQuery } from 'store/user';
import { Transaction } from 'types';
import { fDateTime } from 'utils/format';

const ShowTransaction = ({ transaction }: { transaction: Transaction }) => (
  <tr>
    <td data-label="Date, time of payment  ">
      <span> {fDateTime(transaction.datetime)} </span>
    </td>
    <td data-label="Type of transaction  ">
      <span>
        {' '}
        {transaction.operation_type === 'DEPOSIT'
          ? `Refill ${
              transaction.amount
            } USDT (by ${transaction.currency.toUpperCase()})`
          : `Order a card ${transaction.balance_amount} USDT`}
      </span>
    </td>

    <td data-label="Amount">
      <strong>{transaction.balance_amount} USDT</strong>
    </td>
    <td data-label="Status">
      <strong>{transaction.status.toLowerCase()}</strong>
    </td>
  </tr>
);

const Transactions = () => {
  const transactionsLoadable =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(transactionsQuery);
  const Content = ({ transactions }: { transactions: Transaction[] }) => (
    <tbody>
      {transactions.map((tx) => (
        <ShowTransaction key={tx.id} transaction={tx} />
      ))}
    </tbody>
  );

  switch (transactionsLoadable.state) {
    case 'hasValue':
      return <Content transactions={transactionsLoadable.contents} />;
    case 'loading':
      return <p>Loading</p>;
    case 'hasError':
      return <p>Error</p>;
  }
};

const History = () => {
  const profileLoadable =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(profileQuery);
  // const { client_data: profile } = useRecoilValue(profileQuery)
  return (
    <div className="lk-stat lk__block">
      <div className="lk-stat__balance lk-card__info">
        <span className="lk-card__info-title">Account balance:</span>
        <span className="lk-card__info-desc">
          {profileLoadable?.contents?.client_data?.balance} USDT{' '}
        </span>
      </div>
      <div className="lk-stat__title lk__title">Transactions:</div>
      <div className="lk-stat__table">
        <table>
          <thead>
            <tr>
              <th>Date, time of payment</th>
              <th>Type of transaction</th>
              <th>Amount</th>
              <th>Status</th>
            </tr>
          </thead>
          <Transactions />
        </table>
      </div>
    </div>
  );
};

export default History;
