import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import useAuthActions from '../actions/auth';
// import Link from "../components/Link";

const Register = () => {
  // const params = new Proxy(new URLSearchParams(window.location.search), {
  //   get: (searchParams, prop: string) => searchParams.get(prop),
  // });
  const urlParams = new URLSearchParams(window.location.search);

  const navigate = useNavigate();
  const { register } = useAuthActions();
  const [login, setLogin] = useState(urlParams.get('login') as string);
  const [password, setPassword] = useState('');
  const [showPass, setShowPass] = useState(false);
  const doRegister = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    register(login, password).then(() => navigate('/login'));
  };
  return (
    <>
      <div className="join-section__title">Register</div>
      <form className="form" onSubmit={doRegister}>
        <div className="form__body">
          <div className="form__controls form__controls--email">
            <input
              placeholder="E-mail"
              type="email"
              className="form__input"
              value={login}
              onChange={(e) => setLogin(e.target.value)}
            />
            <span className="" />
          </div>
          <div className="form__controls form__controls--password">
            <input
              placeholder="Password"
              type={showPass ? 'text' : 'password'}
              className="form__input"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <span
              onClick={() => setShowPass(!showPass)}
              className={`form__controls-eyes ${
                !showPass && 'form__controls-eyes--hide'
              }`}
            />
          </div>
          <div className="form__controls">
            <div className="form__controls_flex">
              <div className="form_register">
                <Link to="/login">Login</Link>
              </div>
              <div className="form_forgot">
                <Link to="/reset">Forgot password?</Link>
              </div>
            </div>
          </div>
        </div>
        <div className="form__footer">
          <button className="btn btn-primary" type="submit">
            Register
          </button>
        </div>
      </form>
    </>
  );
};

export default Register;
