import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import useAuthActions from 'actions/auth';

const Login = () => {
  const { signIn } = useAuthActions();
  const [login, setLogin] = useState('');
  const [password, setPassword] = useState('');
  const [showPass, setShowPass] = useState(false);

  const doSignIn = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    signIn(login, password, true);
  };
  return (
    <>
      <div className="join-section__title">Sign in</div>
      <form className="form" onSubmit={doSignIn}>
        <div className="form__body">
          <div className="form__controls form__controls--email">
            <input
              placeholder="E-mail"
              type="email"
              className="form__input"
              value={login}
              onChange={(e) => setLogin(e.target.value)}
            />
            <span className="form__controls--email" />
          </div>
          <div className="form__controls form__controls--password">
            <input
              placeholder="Password"
              type={showPass ? 'text' : 'password'}
              className="form__input"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <span
              onClick={() => setShowPass(!showPass)}
              className={`form__controls-eyes ${
                !showPass && 'form__controls-eyes--hide'
              }`}
            />
          </div>
          <div className="form__controls">
            <label className="form__controls-switcher">
              <div className="switch">
                <input type="checkbox" />
                <span className="slider" />
              </div>
              <div className="switch__text">Remenber me</div>
            </label>
            <div className="form__controls_flex">
              <div className="form_register">
                <Link to="/register">Register now</Link>
              </div>
              <div className="form_forgot">
                <Link to="/reset">Forgot password?</Link>
              </div>
            </div>
          </div>
        </div>
        <div className="form__footer">
          <button className="btn btn-primary">Sign in</button>
        </div>
      </form>
    </>
  );
};

export default Login;
