import React from 'react';

import Mail from 'assets/icons/mail.svg';

const Footer = () => (
  <footer className="footer">
    <div className="container">
      <div className="row">
        <div className="cols-2 ">
          <nav className="footer-menu">
            <div className="footer-menu__title">Menu</div>
            <ul className="footer-menu__list">
              <li className="footer-menu__item">
                <a href="/docs/cloudcard_guava_terms.pdf" target="_blank">
                  Terms and Conditions
                </a>
              </li>
              <li className="footer-menu__item">
                <a href="/docs/privacy_pol.pdf" target="_blank">
                  Privacy Policy
                </a>
              </li>
              <li className="footer-menu__item">
                <a href="/docs/returns_refunds.pdf" target="_blank">
                  Returns and Refund Policy
                </a>
              </li>
            </ul>
          </nav>
        </div>
        <div className="cols-2 ">
          <div className="row-xs">
            <div className="cols-xs-2">
              <div className="footer-contacts">
                <div className="footer-contacts__title">Contact</div>
                <ul className="footer-contacts__list">
                  <li className="footer-contacts__item">
                    <img src={Mail} className="footer-contacts__item-icon" />
                    <a className="footer-contacts__item-text">
                      info@cloudcard.is
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </footer>
);

export default Footer;
