import { useState } from 'react';
import { useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE } from 'recoil';
import { profileQuery } from 'store/user';
import { ClientData } from 'types';
import { SafeEditableField } from '@crypto-stories/shared-ui';
import useAuthActions from '@/actions/auth';

const Profile = () => {
  const profileLoadable =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(profileQuery);
  const { updateProfile, updatePassword } = useAuthActions();
  // const profile = client_data as ClientData
  const [changedName, setChangedName] = useState<string | number>();
  const [changedPhone, setChangedPhone] = useState<string | number>();
  const [changedEmail, setChangedEmail] = useState<string | number>();
  const [currentPassword, setCurrentPassword] = useState<string>();
  const [newPassword, setNewPassword] = useState<string>();
  const [newPasswordConfirm, setNewPasswordConfirm] = useState<string>();
  const doUpdateProfile = () => {
    const profileData = {
      client_name: changedName,
      phone: changedPhone,
      email: changedEmail,
    };
    updateProfile(profileData);
  };

  const doUpdatePassword = () => {
    const passwordData = {
      password: currentPassword,
      new_password: newPassword,
      new_password2: newPasswordConfirm,
    };
    updatePassword(passwordData);
  };
  const Content = ({ profile }: { profile: ClientData }) => (
    <div className="lk-profile ">
      <div className="lk__block lk-step lk__block--profile">
        <div className="lk-step__fields">
          <SafeEditableField
            key="changeName"
            title="Name"
            value={changedName || profile.client_name}
            onConfirm={(val) => setChangedName(val)}
          />
          <SafeEditableField
            title="E-Mail"
            value={changedEmail || profile.email}
            onConfirm={(val) => setChangedEmail(val)}
          />

          <SafeEditableField
            title="PhoneNumber"
            value={changedPhone || profile.phone}
            onConfirm={(val) => setChangedPhone(val)}
          />
          <div className="lk-step__fields">
            <button
              className="lk-currency__btn lk__block-submit left"
              onClick={doUpdateProfile}
            >
              Update
            </button>
          </div>
        </div>
      </div>
      <div className="lk__block lk-step lk__block--profile">
        <div className="lk-step__fields ">
          <SafeEditableField
            title="Current password"
            value={currentPassword}
            inputType="password"
            onConfirm={(val) => setCurrentPassword(val as string)}
            hideBtnEdit
          />
          <SafeEditableField
            title="New password"
            value={newPassword}
            inputType="password"
            onConfirm={(val) => setNewPassword(val as string)}
            hideBtnEdit
          />

          <SafeEditableField
            title="Confirm new password"
            value={newPasswordConfirm}
            inputType="password"
            onConfirm={(val) => setNewPasswordConfirm(val as string)}
            hideBtnEdit
          />
          {/* <fieldset className="fg fg--editable"> */}
          {/*    <label>New password</label> */}
          {/*    <input type="password" value=""/> */}
          {/*    <button className="fg__edit"> */}
          {/*        <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg"> */}
          {/*            <path fillRule="evenodd" clipRule="evenodd" */}
          {/*                  d="M10.7053 3.23934V3.23934C9.80928 2.56734 8.53861 2.74867 7.86661 3.64401C7.86661 3.64401 4.52461 8.09601 3.36528 9.64067C2.20594 11.186 3.30261 13.1007 3.30261 13.1007C3.30261 13.1007 5.46528 13.598 6.60794 12.0747C7.75128 10.552 11.1093 6.07801 11.1093 6.07801C11.7813 5.18267 11.6006 3.91134 10.7053 3.23934Z" */}
          {/*                  stroke="black" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"></path> */}
          {/*            <path d="M7.00293 4.80762L10.2456 7.24162" stroke="black" strokeWidth="1.5" */}
          {/*                  strokeLinecap="round" strokeLinejoin="round"></path> */}
          {/*        </svg> */}
          {/*    </button> */}
          {/* </fieldset> */}
        </div>

        <div className="lk-step__fields">
          <button
            onClick={doUpdatePassword}
            className="lk-currency__btn lk__block-submit left"
          >
            Save
          </button>
        </div>
      </div>
    </div>
  );
  switch (profileLoadable.state) {
    case 'hasValue':
      return (
        <Content profile={profileLoadable.contents.client_data as ClientData} />
      );
    case 'loading':
      return <p>Loading</p>;
    case 'hasError':
      return <p>Error</p>;
  }
};

export default Profile;
