import useOrderActions from '@/actions/order';
import { depositAmountState, depositCurrencyState } from '@/store/deposit';
import { orderState, pricesQuery } from '@/store/order';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  useRecoilState,
  useRecoilValue,
  useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE,
  useSetRecoilState,
} from 'recoil';
import { profileQuery } from '../store/user';
import { ReactComponent as IconArrows } from '@/assets/icons/drag-arrows.svg';

// @ts-ignore
import 'rc-slider/assets/index.css';

import { Select } from '@/components/Select';
import Slider from 'rc-slider';
import { ArrowIcon } from '@/components/icons';

const BalanceChoose = () => {
  const prices = useRecoilValue(pricesQuery);
  const [currentOrder, setCurrentOrder] = useRecoilState(orderState);
  const marks = prices.reduce<Record<string, string>>((acc, cur, idx) => {
    acc[idx.toString(10)] = `${parseFloat(cur.balance_amount).toFixed()} `;
    return acc;
  }, {});

  // const [selectedIbdex, setSelectedIndex] = useState(0);

  const selectItem = (idx: number | number[]) => {
    setCurrentOrder({
      ...currentOrder,
      price: prices[idx as number],
    });
  };
  if (!currentOrder) {
    selectItem(0);
  }
  const calcSelectedIndex = () => {
    if (!currentOrder?.price) {
      return 0;
    }
    return prices.findIndex(
      (priceItem) =>
        currentOrder?.price?.balance_amount === priceItem.balance_amount
    );
  };
  const selectedIndex = calcSelectedIndex();

  return (
    <div className="lk-step lk__block lk__block--small-pad lk__block--choose">
      <div className="lk-step__title lk__title">
        <i>1</i>
        <span>Choose card balance</span>
        <IconArrows className="lk__icon" />
      </div>
      <Select
        selectedIndex={selectedIndex}
        setSelectedIndex={selectItem}
        options={prices}
      />
      {/* <div className="">
        <p className="selected-balance">
          {currentOrder?.price?.balance_amount || prices[0].balance_amount}  (
          {currentOrder?.price?.price || prices[0].price} &nbsp; USDT)
        </p>
      </div> */}
      <div className="slider-wrapper">
        <Slider
          min={0}
          marks={marks}
          step={null}
          included={false}
          onChange={selectItem}
          handleRender={(props: any) => (
            <div {...props.props}>
              <div className='arrow-card-handler'>
                <div className='arrow-icon-card arrow-icon-card--left'>
                  <ArrowIcon />
                </div>
                {Number(currentOrder?.price?.balance_amount).toFixed(0)} 
                <div className='arrow-icon-card arrow-icon-card--right'>
                  <ArrowIcon dir="right" />
                </div>
              </div>
            </div>
          )}
          handleStyle={{
            color: '#000',
            fontSize: 22,
            background: '#FFE949',
            borderRadius: 14,
          }}
          defaultValue={
            currentOrder?.price
              ? prices.findIndex(
                (priceItem) =>
                  currentOrder?.price?.balance_amount ===
                  priceItem.balance_amount
              )
              : 0
          }
          value={selectedIndex}
          max={Object.keys(marks).length - 1}
        />
        <div className="slider-result">
          <span>{currentOrder?.price?.balance_amount} </span>
          {currentOrder?.price?.price} USDT
        </div>
      </div>
      <div className="container-slider">
        <span className="container-slider__item">15 </span>
        <span className="container-slider__item container-slider__item--last">
          150 
        </span>
      </div>
    </div>
  );
};

