const EnterData = () => {
  const [currentOrder, setCurrentOrder] = useRecoilState(orderState);
  const updatePhone = (event: React.ChangeEvent<HTMLInputElement>) => {
    const phoneNum: string | undefined = event.target.value;
    // if(phoneNum === '') {
    //     phoneNum = undefined
    // }
    const holder = phoneNum === '' ? undefined : { phone: phoneNum };
    setCurrentOrder({ ...currentOrder, holder });
  };
  return (
    <div className="lk-step lk__block " id="enter-data">
      <div className="lk-step__title lk__title">
        <i>2</i>
        <span>Enter your data</span>
      </div>
      <div className="lk-step__fields">
        <fieldset className="fg">
          <label>Phone number</label>
          <input
            className="field"
            type="tel"
            value={currentOrder?.holder?.phone}
            onChange={updatePhone}
          />
          <span className="fg__hint">*For 3D Secure</span>
        </fieldset>
      </div>
    </div>
  );
};

const Finish = () => {
  const navigate = useNavigate();
  const currentOrder = useRecoilValue(orderState);
  // const [deposit, setDeposit] = useRecoilState(depositState)
  const setDepositCurrency = useSetRecoilState(depositCurrencyState);
  const setDepositAmount = useSetRecoilState(depositAmountState);

  const profile =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(profileQuery);

  const needRefill = parseFloat(
    (
      parseFloat(currentOrder?.price?.price as string) -
      parseFloat(profile.contents?.client_data?.balance as string)
    ).toFixed(2)
  );

  const enoughMoney = needRefill <= 0;

  // const profileLoader =  (profileQuery)
  // const enoughMoney = parseFloat(profileLoader.contents?.client_data?.balance as string) > parseFloat(currentOrder?.price?.balance_amount as string)
  // const enoughMoney = true
  const { confirmOrder } = useOrderActions();
  const processButton = () => {
    if (enoughMoney) {
      confirmOrder();
      navigate('/');
    } else {
      setDepositCurrency('usdt-tron');
      setDepositAmount(needRefill.toString());
      navigate('/deposit/');
    }
  };
  return (
    <div className="lk-step lk__block lk__block--small-pad lk__block--choose">
      <div className="lk-step__title lk__title">
        <i>3</i>
        <span>Confirm order</span>
      </div>
      <div className="lk-currency">
        <div className="lk-currency__content" data-tabs-path="1">
          <div className="lk-currency__desc lk__desc">
            {enoughMoney
              ? 'Click on the button to confirm'
              : 'Insufficient funds to order those card'}
          </div>
          <button
            onClick={processButton}
            className="lk-currency__btn"
            disabled={!currentOrder?.price || !currentOrder?.holder}
          >
            {enoughMoney
              ? 'Order CloudCard'
              : `Refill balance for ${needRefill} USD`}
          </button>
        </div>
      </div>
    </div>
  );
};

const NewCard = () => {
  const currentOrder = useRecoilValue(orderState);
  // useEffect(() => {
  //   if (currentOrder?.price) moveToEnterData();
  // });
  return (
    <div className="">
      <BalanceChoose />
      <EnterData />
      <Finish />
    </div>
  );
};

export default NewCard;
