import { parseISO, format, formatDistance } from 'date-fns';
// import numeral from 'numeral'
import { card } from 'creditcards';
import parsePhoneNumber from 'libphonenumber-js';

export const fDateTime = (input: string) => {
  const date = parseISO(input);
  return format(date, 'dd.MM.yyyy | HH:mm');
};

export const fCardExpiresIn = (input: string) => {
  const dateFrom = new Date();
  const [month, year] = input.split('/');
  const dateTo = new Date(parseInt(`20${year}`, 10), parseInt(month, 10));
  return formatDistance(dateTo, dateFrom);
};

export const fPan = (input: string) => card.format(input);

export const fPhone = (input: string) => {
  const phone = parsePhoneNumber(`+${input}`);
  return phone?.formatInternational();
};

export const hide = (input: string) => {
  if (input.length > 5) {
    return `${input.slice(0, 4)} **** **** ${input.slice(-4)}`;
  }
  return Array(input.length).fill('*').join('');
};
