import { useParams } from 'react-router-dom';
import {
  useRecoilValue,
  useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE,
} from 'recoil';
import { selectCard, statementQuery } from 'store/user';
import { IssuedCard, Statement } from 'types';

import { fCardExpiresIn, fDateTime, fPan, hide } from '../utils/format';

// import cardTop from "../assets/img/card-top.svg";

type StatementsParam = {
  id: string;
};

const StatmentRow = ({ row }: { row: Statement }) => (
  <tr>
    <td data-label="Date and time">{fDateTime(row.transactionDate)}</td>
    <td data-label="RRN">{row.rrn}</td>
    <td data-label="Merchant">
      {row.merchantInfo.name}, {row.merchantInfo.country}
    </td>
    <td data-label="Type">{row.transactionType}</td>
    <td data-label="Billing amount">
      <strong>
        {row.billingAmount.amount} {row.billingAmount.currency}
      </strong>
    </td>
    <td data-label="Transaction amount">
      <strong>
        {row.transactionAmount.amount} {row.transactionAmount.currency}
      </strong>
    </td>
  </tr>
);

const StatemntDetail = ({ cardId }: { cardId: string }) => {
  const statementLoadable = useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(
    statementQuery(cardId)
  );
  const Content = ({ statement }: { statement: Statement[] }) => (
    <div className="balance-b__table">
      <table cellPadding="0" cellSpacing="0">
        <thead>
          <tr>
            <th>Date and time</th>
            <th>RRN</th>
            <th>Merchant</th>
            <th>Type</th>
            <th>Billing amount</th>
            <th>Transaction amount</th>
          </tr>
        </thead>

        <tbody>
          {statement.map((row, id) => (
            <StatmentRow key={id} row={row} />
          ))}
        </tbody>
      </table>
    </div>
  );
  switch (statementLoadable.state) {
    case 'hasValue':
      return <Content statement={statementLoadable.contents} />;
    case 'loading':
      return <p>Loading</p>;
    case 'hasError':
      return <p>Error</p>;
  }
};

const Statesment = () => {
  const { id } = useParams<StatementsParam>();
  // const [startTransition, isPending] = useTransition({ timeoutMs: 3000 });
  const card = useRecoilValue(selectCard(id as string)) as IssuedCard;

  return (
    <div className="balance-b">
      <div className="balance-b__grid">
        <div className="balance-b__column">
          <div className="balance-b__card">
            <div className="balance-b__card-photo">
              {/* <div className="balance-b__card-info">
                                <div className="balance-b__card-number">{}</div>
                                <div className="balance-b__card-name">Daniel Austin</div>
                                <div className="balance-b__card-date">02/30</div>
                                </div> */}
              <img
                className="card"
                src={`https://api.cloudcard.is/img_cc/render.php?pan=${hide(
                  fPan(card.pan)
                )}&exp=${card.exp}&cvc=${hide(card.cvc)}`}
                alt=""
              />
            </div>
            <div className="balance-b__card-about">
              <div className="balance-b__card-list">
                <div className="balance-b__card-field">
                  <div className="balance-b__card-label">Card number</div>
                  <div className="balance-b__card-value">{card.masked_pan}</div>
                </div>
                <div className="balance-b__card-field">
                  <div className="balance-b__card-label">Expiration</div>
                  <div className="balance-b__card-value">{card.exp}</div>
                </div>
                <div className="balance-b__card-field">
                  <div className="balance-b__card-label">Card ID</div>
                  <div className="balance-b__card-value">{card.id}</div>
                </div>
                <div className="balance-b__card-field">
                  <div className="balance-b__card-label">Available time</div>
                  <div className="balance-b__card-value">
                    {fCardExpiresIn(card.exp)}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="balance-b__column balance-b__column_rest">
          <div className="balance-b__rest">
            <div className="balance-b__rest-label">Balance</div>
            <div className="balance-b__rest-value">{card.balance} EUR</div>
          </div>
        </div>
      </div>

      <div className="balance-b__main">
        <div className="balance-b__title">
          Statement for <span>{card.masked_pan}</span>
        </div>
        <StatemntDetail cardId={card.id} />
      </div>
    </div>
  );
};

export default Statesment;
