import { atom, selector, selectorFamily } from 'recoil';
import { pricesRequest } from '@/utils/api';
import { CardPriceItem, Order } from '@/types';
import persistEffect from '@/store/effects';
// import Deposit from "@type";

export const pricesQuery = selector<CardPriceItem[]>({
  key: 'pricesQuery',
  get: () => pricesRequest(),
});

export const orderState = atom<Order>({
  key: 'orderState',
  default: {},
  effects: [persistEffect('orderState')],
});

export const selectPriceObject = selectorFamily({
  key: 'selectPriceObject',
  get:
    (price: string) =>
    ({ get }) =>
      get(pricesQuery).find((priceItem) => priceItem.price === price),
});
