import React, { FC } from "react";

import { ReactComponent as IconClose } from "@/assets/icons/close.svg";
import useAlertsActions from "@/actions/alerts";

type Props = {
  id: string;
  message: string;
  type: "success" | "error";
};

export const Alert: FC<Props> = ({ id, message, type }: Props) => {
  const { closeAlert } = useAlertsActions();
  return (
    <div className={`alert ${type}`}>
      <div className="alert-inner">
        <IconClose className="alert__close" onClick={() => closeAlert(id)} />
        <h4 className="alert__title">{type}</h4>
        <p className="alert__desc">{message}</p>
      </div>
    </div>
  );
};
