import React, { useState, FC } from 'react';
import { ReactComponent as IconCopy } from '@/assets/icons/copy.svg';
import { ReactComponent as IconEye } from '@/assets/icons/eye.svg';
import { ReactComponent as IconEyeClose } from '@/assets/icons/eye_close.svg';
import { fPan, hide } from '../utils/format';
import CopyArea from './CopyArea';

type Props = {
  label: string;
  text: string;
  small?: boolean;
  alwaysShow?: boolean;
  middle?: boolean;
  className?: string;
};

export const MaskedField: React.FC<Props> = ({
  text,
  small,
  label,
  alwaysShow,
  middle,
  className,
}: Props) => {
  const [show, setShow] = useState(alwaysShow);

  const showToggle = (e: React.MouseEvent) => {
    e.stopPropagation();
    setShow(!show);
  };

  return (
    <CopyArea text={text}>
      <div
        className={`lk-card__info copy-panel ${small ? 'small' : ''} ${
          middle ? 'middle' : ''
        } ${className}`}
      >
        <span className="lk-card__info-title">{label}</span>
        <div className="fg fg--bg">
          <div className="fg fg--bg">
            <div className="input">{show ? fPan(text) : hide(text)}</div>
            {show && <IconCopy className="copy" />}
          </div>
          <button className="copy-btn" onClick={showToggle}>
            {!alwaysShow &&
              (show ? (
                <IconEye />
              ) : (
                <IconEyeClose
                  style={{ marginTop: text.length > 5 && !show ? 0 : -7 }}
                />
              ))}
          </button>
        </div>
      </div>
    </CopyArea>
  );
};
