import {
  useRecoilValue,
  useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE,
} from 'recoil';
import { profileQuery } from 'store/user';
import { IssuedCard } from 'types';

import { Link } from 'react-router-dom';
import { fPan, fPhone, hide } from 'utils/format';
import useOrderActions from '@/actions/order';
import { SafeEditableField } from '@crypto-stories/shared-ui';
import { MaskedField } from '../components/MaskedField';

type ShowCardProps = {
  card: IssuedCard;
};

const ShowCard = ({ card }: ShowCardProps) => {
  const { issued_cards: cards } = useRecoilValue(profileQuery);
  // const [changedPhone, newChangedPhone] = useState(card.phone)
  const { updateCardPhone } = useOrderActions();
  return (
    <div className="lk-card lk__block" key={`card-${card.id}`}>
      {cards[0].id === card.id && (
        <div className="lk__title lk-card__title">
          All cards you purchased are displayed here
        </div>
      )}
      <div className="lk-card__wrap">
        <div className="lk-card__img">
          <img
            src={`https://api.cloudcard.is/img_cc/render.php?pan=${hide(
              fPan(card.pan)
            )}&exp=${card.exp}&cvc=${hide(card.cvc)}`}
            alt=""
          />
        </div>
        <div className="lk-card__content">
          <div className="lk-card__fields">
            <MaskedField
              label="Card number"
              text={card.pan}
              className="order"
            />

            <MaskedField
              label="Expiry date"
              text={card.exp}
              small
              alwaysShow
              middle
            />
            <MaskedField label="CVC" text={card.cvc} small />
            <div className="lk-card__info--phone lk-card__info order">
              <SafeEditableField
                title="Phone number"
                value={fPhone(card.phone)}
                onConfirm={(val) => {
                  if (val !== card.phone) {
                    updateCardPhone(card.id, val as string);
                  }
                }}
              />
            </div>
            <div className="lk-card__info lk-card__info--desc">
              <span className="lk-card__info-title">Card balance</span>
              <span className="lk-card__info-desc"> {card.balance}</span>
            </div>
          </div>
          <Link to={`/transactions/${card.id}`} className="lk-card__link">
            Check balance and card transactions
          </Link>
        </div>
      </div>
    </div>
  );
};

const MyCards = () => {
  const profileLoadable =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(profileQuery);
  // const { issued_cards: cards } = useRecoilValue(profileQuery)
  switch (profileLoadable.state) {
    case 'hasValue':
      return (
        <div className="lk-cards">
          {profileLoadable.contents.issued_cards.length > 0 ? (
            profileLoadable.contents.issued_cards.map((card) => (
              <ShowCard card={card} key={card.id} />
            ))
          ) : (
            <h4 className="lk-card lk__block">
              You dont have issues cards.{' '}
              <Link to="/new">Issue your first card </Link>
            </h4>
          )}
        </div>
      );
    case 'loading':
      return <p>Loading</p>;
    case 'hasError':
      return <p>Error</p>;
  }
};

export default MyCards;
