import { useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE } from 'recoil';
import { profileQuery, transactionsQuery } from 'store/user';
import { Notification, Transaction } from 'types';
import { fDateTime } from 'utils/format';
import { notificationsQuery } from '../store/notifications';

const ShowNotification = ({ notification }: { notification: Notification }) => (
  <tr>
    <td>
      <span> {fDateTime(notification.datetime)} </span>
    </td>
    <td>
      <span>{notification.text}</span>
    </td>
  </tr>
);

const NotificationsDetail = ({
  notifications,
}: {
  notifications: Notification[];
}) => {
  const notificationsLoadable =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(notificationsQuery);
  // { notifications }: { notifications: Notification[] }
  const Content = () => (
    <tbody>
      {notifications.map((tx) => (
        <ShowNotification key={tx.id} notification={tx} />
      ))}
    </tbody>
  );

  switch (notificationsLoadable.state) {
    case 'hasValue':
      return <Content />;
    case 'loading':
      return <p>Loading</p>;
    case 'hasError':
      return <p>Error</p>;
  }
};

const Notifications = () => {
  const profileLoadable =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(profileQuery);
  // const { client_data: profile } = useRecoilValue(profileQuery)
  const notificationsLoadable =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(notificationsQuery);

  return (
    <div className="lk-stat lk__block">
      {/* <div className="lk-stat__balance lk-card__info">
        <span className="lk-card__info-title">Account balance:</span>
        <span className="lk-card__info-desc">
          {profileLoadable?.contents?.client_data?.balance} USDT{' '}
        </span>
      </div> */}
      <div className="lk-stat__title lk__title">Notifiations:</div>
      {notificationsLoadable.contents.length !== 0 ? (
        <div className="lk-stat__table">
          <table>
            <thead>
              <tr>
                <th>Datetime</th>
                <th style={{ textAlign: 'left' }}>Message</th>
              </tr>
            </thead>
            <NotificationsDetail
              notifications={notificationsLoadable.contents as Notification[]}
            />
          </table>
        </div>
      ) : (
        <span className="lk-stat__descr">Where is no events yet</span>
      )}
    </div>
  );
};

export default Notifications;
