import {
  useRecoilState,
  useRecoilValue,
  useRecoilRefresher_UNSTABLE,
} from 'recoil';
import useAlertsActions from '@/actions/alerts';
import { tokenState } from '@/store/auth';
import { orderState } from '@/store/order';
import { authRequest } from '@/utils/api';
import { transactionsQuery } from '@/store/user';

const useOrderActions = () => {
  const { addAlert } = useAlertsActions();
  const [order, setOrder] = useRecoilState(orderState);
  const token = useRecoilValue(tokenState);
  const refreshHistory = useRecoilRefresher_UNSTABLE(transactionsQuery);

  const confirmOrder = async () => {
    const result = await authRequest('/order_card', token as string, {
      balance_amount: order.price?.balance_amount,
      phone: order.holder?.phone,
      enablePushNotification: 1,
    });
    if (result.status === 'ok') {
      addAlert('success', 'Order created. Card will apear soon');
    } else {
      addAlert('error', 'Order error');
    }
    setOrder({});
    refreshHistory();
    // navigate('/')
  };

  const updateCardPhone = async (cardId: string, newPhone: string) => {
    const result = await authRequest('/update_phone', token as string, {
      id: cardId,
      phone: newPhone,
    });
    if (result.status === 'ok') {
      addAlert('success', `Phone number changed to ${newPhone}`);
    } else {
      addAlert('error', 'Phone changing error');
    }
    // refreshProfile()
  };

  return { confirmOrder, updateCardPhone };
};

export default useOrderActions;
