import React from "react";
import { Link } from "react-router-dom";

const ForgotPassword = () => <>
           <div className="join-section__title">
                            Forgot password
                        </div>
                        <div className="main__subtitle">
                            Enter your email address
                        </div>
                        <form className="form">
                            <div className="form__body">
                                <div className="form__controls form__controls--email">
                                    <input placeholder="E-mail" type="email" className="form__input" />
                                    <span className="form__controls-eyes" />
                                </div>
                                <div className="form__controls">
                                    <div className="form__controls_flex">
                                        <div className="form_register">
                                            <Link to="/login">Login</Link>
                                        </div>
                                        <div className="form_forgot">
                                            <Link to="/register">Register</Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="form__footer">
                                <button className="btn btn-primary">
                                    Continue
                                </button>
                            </div>
                        </form>
    </>;

export default ForgotPassword