import { Navigate, Outlet } from 'react-router-dom';

import React from 'react';
import { useRecoilValue } from 'recoil';
import CoinsBanner from '@/assets/img/cards_banner.svg';
import { isSignedInState } from '@/store/auth';

const Public = () => {
  const isSignedIn = useRecoilValue(isSignedInState);
  const Content = (
    <section className="main_screen login">
      <div className="container">
        <div className="main">
          <div className="main__content">
            <div className="join-section__form">
              <Outlet />
            </div>
            <div className="main__banner">
              <img src={CoinsBanner} className="main__banner-image" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
  if (isSignedIn) {
    return <Navigate to="/" />;
  }

  return Content;
};

export default Public;
