import React, { useState, useRef, FC } from 'react';
import { ReactComponent as Icon } from '@/assets/icons/arrow-down.svg';
import useOnClickOutside from '../hooks/useOutside';
import { CardPriceItem } from '@/types';

type TSelectProps = {
  options: CardPriceItem[];
  placeholder?: string;
  selectedIndex: number;
  setSelectedIndex: (selectedIndex: number) => void;
};

export const Select: FC<TSelectProps> = ({
  options,
  placeholder,
  selectedIndex,
  setSelectedIndex,
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [activeList, setActiveList] = useState(0);

  const ref = useRef(null);
  const toggling = () => setIsOpen(!isOpen);

  const onOptionClicked = (value: any, idx: number) => {
    setActiveList(idx);
    setIsOpen(false);
    setSelectedIndex(options.indexOf(value));
  };

  const handleClickOutside = () => {
    setIsOpen(false);
  };

  useOnClickOutside(ref, handleClickOutside);

  const onKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'ArrowUp') {
      e.preventDefault();
      if (activeList === 0) {
        return;
      }
      setActiveList(activeList - 1);
    } else if (e.key === 'ArrowDown') {
      if (activeList >= options.length - 1) {
        return setActiveList(0);
      }
      setActiveList(activeList + 1);
    } else if (e.key === 'Enter') {
      setIsOpen(false);
    }
  };

  return (
    <div className="dropdown-container" ref={ref}>
      <div
        className="dropdown-header"
        tabIndex={0}
        onClick={toggling}
        onKeyDown={onKeyDown}
      >
        <div className="dropdown-content">
          <span>
            {(selectedIndex && options[selectedIndex].balance_amount) ||
              options[0].balance_amount}{' '}
            {' '}
            <small className="dropdown-header__small">
              {(selectedIndex && options[selectedIndex].price) ||
                options[0].price}{' '}
              USDT
            </small>
          </span>
          <div
            className={`dropdown-arrow ${
              isOpen ? 'dropdown-arrow--rtt' : null
            }`}
          >
            <Icon />
          </div>
        </div>
        <span data-label={placeholder}></span>
      </div>
      {isOpen && (
        <div className="dropdown-list">
          {options.map((option, idx) => (
            <div
              className="dropdown-list__item"
              //   active={activeList === idx}
              onClick={() => onOptionClicked(option, idx)}
              key={idx}
            >
              <div className="dropdown-list__text">
                {option.balance_amount} 
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};
