import { useRecoilState, useRecoilValue } from 'recoil';

import { tokenState } from 'store/auth';
// import useRequest from './request';
import { authRequest, request } from 'utils/api';
import useAlertsActions from '@/actions/alerts';
import { profileQuery } from '@/store/user';

const useAuthActions = () => {
  const [token, setToken] = useRecoilState(tokenState);
  const profile = useRecoilValue(profileQuery);
  const { addAlert } = useAlertsActions();
  // const { request, authRequest } = useRequest()

  const signIn = (login: string, password: string, rememberMe: boolean) => {
    request('/login', { login, password }).then(
      ({ status, token: newToken }) => {
        if (status === 'ok') {
          setToken(newToken as string);
          if (rememberMe) {
            localStorage.setItem('token', newToken as string);
          }
          addAlert('success', 'Successfully signed in');
        } else {
          addAlert('error', 'Invalid login or password');
        }
      }
    );
  };

  const register = (login: string, password: string) =>
    request('/register', { login, password }).then(() => {
      addAlert('success', 'Successfully registered');
    });

  const signOut = () => {
    setToken(null);
    localStorage.removeItem('token');
    addAlert('success', 'Successfully signed out');
  };

  type UpdateProfileData = {
    client_name?: string | number;
    email?: string | number;
    phone?: string | number;
  };
  const updateProfile = async ({
    client_name,
    email,
    phone,
  }: UpdateProfileData) => {
    const dataToUpdate: UpdateProfileData = {};
    if (profile.client_data?.client_name !== client_name) {
      dataToUpdate.client_name = client_name;
    }
    if (profile.client_data?.email !== email) {
      dataToUpdate.email = email;
    }
    if (profile.client_data?.phone !== phone) {
      dataToUpdate.phone = phone;
    }
    if (dataToUpdate.email || dataToUpdate.phone || dataToUpdate.client_name) {
      const result = await authRequest(
        '/update_profile',
        token as string,
        dataToUpdate
      );
      if (result.status === 'ok') {
        addAlert('success', 'Profile successfully updated');
      } else {
        addAlert('error', 'Something went wrong');
      }
    }
  };
  // TODO strict value
  type UpdatePasswordData = {
    password: string | undefined;
    new_password: string | undefined;
    new_password2: string | undefined;
  };
  const updatePassword = async (data: UpdatePasswordData) => {
    if (data.password && data.new_password) {
      const result = await authRequest(
        '/change_password',
        token as string,
        data
      );
      if (result.status === 'ok') {
        addAlert('success', 'Password successfully updated');
      } else {
        addAlert('error', 'Passwords does not match');
      }
    }
  };

  return { signIn, signOut, register, updateProfile, updatePassword };
};

export default useAuthActions;
