import useAuthActions from 'actions/auth';
import CloudcardLogo from 'assets/img/cloudcard-logo.svg';
import { Link } from 'react-router-dom';
import { ReactComponent as IconLogo } from '@/assets/icons/logo1.svg';

const Header = () => {
  const { signOut } = useAuthActions();
  return (
    <div className="page-b__top">
      <div className="page-b__top-flex">
        <div className="page-b__logo">
          <Link to="/">
            <IconLogo />
          </Link>
        </div>
        <div className="header-b__menu">
          <Link
            to="/profile"
            className="header-b__btn header-b__links header-b__links_border"
          >
            <svg
              width="29"
              height="29"
              viewBox="0 0 29 29"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10.9702 19.497H17.9967"
                stroke="#383838"
                strokeWidth="1.8125"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <path
                fillRule="evenodd"
                clipRule="evenodd"
                d="M2.90015 16.57C2.90015 9.76583 3.64206 10.2407 7.6356 6.53716C9.38285 5.13066 12.1016 2.41675 14.4494 2.41675C16.796 2.41675 19.5691 5.11737 21.3321 6.53716C25.3256 10.2407 26.0663 9.76583 26.0663 16.57C26.0663 26.5834 23.6992 26.5834 14.4832 26.5834C5.26727 26.5834 2.90015 26.5834 2.90015 16.57Z"
                stroke="#383838"
                strokeWidth="1.8125"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </Link>
          <a
            href=""
            className="header-b__btn header-b__links header-b__links_border"
            onClick={signOut}
          >
            <svg
              width="29"
              height="29"
              viewBox="0 0 29 29"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M26.3308 14.6459H11.7812"
                stroke="#383838"
                strokeWidth="1.8125"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <path
                d="M22.7944 11.1223L26.3324 14.6458L22.7944 18.1693"
                stroke="#383838"
                strokeWidth="1.8125"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <path
                d="M19.7681 9.21942C19.3693 4.89359 17.7501 3.32275 11.3097 3.32275C2.72936 3.32275 2.72936 6.114 2.72936 14.4998C2.72936 22.8857 2.72936 25.6769 11.3097 25.6769C17.7501 25.6769 19.3693 24.1061 19.7681 19.7803"
                stroke="#383838"
                strokeWidth="1.8125"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Header;
