// TODO: it stab for cards payment. need to be finished when payment method will work on back
const moveToSpecifyAmount = () => {
  setTimeout(() =>
    document
      .getElementById('specify-amount')
      ?.scrollIntoView({ behavior: 'smooth', block: 'start' })
  );
};

