import React from 'react';

type Props = {
  value: number;
};

export const Timeline = ({ value }: Props) => {
  return (
    <div className="timeline">
      <div className="timeline-item">
        <div className="timeline-wrap">
          <div className={`timeline__number ${value > 0 ? 'active' : null}`}>
            1
          </div>
        </div>
        <p className="timeline__text">Choose currency</p>
      </div>
      <div className="timeline-item">
        <div className="timeline-wrap">
          <div className={`timeline__number ${value > 1 ? 'active' : null}`}>
            2
          </div>
        </div>
        <p className="timeline__text">Specify amount</p>
      </div>
      <div className="timeline-item">
        <div className="timeline-wrap">
          <div className={`timeline__number ${value > 2 ? 'active' : null}`}>
            3
          </div>
        </div>
        <p className="timeline__text">Pay</p>
      </div>
      <div className="timeline-item">
        <div className="timeline-wrap">
          <div className={`timeline__number ${value > 3 ? 'active' : null}`}>
            4
          </div>
        </div>
        <p className="timeline__text">Confirmation</p>
      </div>
    </div>
  );
};
